import { NavLink } from "react-router-dom"
import "./Navbar.scss"
import userProfile from "../../assets/images/navbar-icons/user-profile.svg"
import ariaGranolLogo from "../../assets/images/aria-granol-logo.png"
const Navbar = () => {
 

  return (
    <div className="custom-navbar">
      <div className="logo-container">
        <NavLink to="/" className="">
          <img className="logo-image"
            src={ariaGranolLogo} alt="logo" />
        </NavLink>

      </div>
      <div className="user-profile-container">
        <div className="user-profile">
          <span>
            آریا گرانول
          </span>
          <span>
            - - -
          </span>
        </div>
        <NavLink to="/user/profile" className="">
          <img src={userProfile}
            className="user-profile-image" alt="user-proffile"
          // onClick={() => setShowUserProfilePopup(!showUserProfilePopup)}
          />
        </NavLink>
        {/* <div className="user-profile-popup">
          <span>
            ویرایش پروفایل
          </span>
          <span>
            خروج از حساب کاربری
          </span>
        </div> */}
      </div>
      <div className="hamburger-menu-placeholder"></div>
    </div>
  )
}

export default Navbar