import Axios from 'axios'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'

import "./login.scss"

import closeEye from "../../assets/images/eye-close-white.svg"
import openEye from "../../assets/images/eye-open-white.svg"
import LoadingForButton from "../../layouts/LoadingForButton/LoadingForButton"
const Login = () => {
    const apiAddress = process.env.REACT_APP_Api_Address
    const loginRoute = "/customerLogin"
    const navigate = useNavigate()
    const [loginInfo, setLoginInfo] = useState();
    const [showPassword, setShowPassword] = useState(false);
    const [userPassIsWrong, setUserPassIsWrong] = useState(false);
    const [inProccess, setInProccess] = useState(false);

    const handleLoginInfoChange = (e) => {
        userPassIsWrong && setUserPassIsWrong(false)

        const value = e.target.value;
        const name = e.target.name;
        setLoginInfo({ ...loginInfo, [name]: value });
    };

    const handleLogin = (e) => {
        setInProccess(true)
        e.preventDefault();
        // form.resetFields()
        Axios.post(apiAddress + loginRoute,
            loginInfo).then(async res => {
                if (res.data.login === true) {
                    try {
                        // get api token and save in localstorage
                        await localStorage.setItem("token", res.data.api_token)
                        // account owner name
                        await localStorage.setItem("user", res.data.name)
                        await localStorage.setItem("complex_nik_name", res.data.hotel_name)
                        await localStorage.setItem("complex_logo", res.data.logo)
                        // for  advertise we save job cateegory of complex
                        await localStorage.setItem("complex_job_category", res.data.job_category)
                        navigate("/")
                    }
                    catch {
                        alert("در ذخیره پارامترهای ورودی مشکلی پیش آمده")
                    }
                }
                else {
                    alert("در ذخیره پارامترهای ورودی مشکلی پیش آمده")
                }
            }

            ).catch(err => {
                if (err.response.status === 401) {
                    setUserPassIsWrong(true)
                }

                else {
                    alert(err.response.data.message)
                }

            }
            ).finally(() => setInProccess(false))
    }

    const signUp = () => {
        alert("لطفا جهت تهیه اشتراک و یا بازیابی رمز عبور با پشتیبانی به شماره 32176-025 تماس بگیرید!")
    }


    return (

        <div className='h-100vh w-100vw d-flex justify-content-center align-items-center login-background'>
            <div className='login-container'>
                <form className='d-flex flex-column align-items-center justify-content-start h-100 mt-4'
                    autocomplete="off" onSubmit={handleLogin}>

                    <p className={`${userPassIsWrong ? "wrong-login-message-show" : null} wrong-login-message`}>
                        نام کاربری و رمز عبور وارد شده مطابقت ندارند
                    </p>
                    <div className="login-input-box">
                        <input type="text" id="username" name="username"
                            placeholder="username"
                            onChange={handleLoginInfoChange}
                            required
                        />
                        <label htmlFor="password">نام کاربری</label>
                    </div>

                    <div className="login-input-box">
                        <input type={showPassword ? "text" : "password"} id="password" name="password"
                            placeholder="password"
                            required
                            onChange={handleLoginInfoChange}
                        />
                        <label htmlFor="password">رمز عبور</label>
                        <img src={showPassword ? closeEye : openEye} className="show-hide-password-icon"
                            onClick={() => setShowPassword(!showPassword)}
                            alt="icon" />
                    </div>

                    <p className='signup' onClick={signUp}>
                        عضویت/فراموشی رمز
                    </p>

                    <button disabled={inProccess} className='login-button'  >
                        {inProccess ? <LoadingForButton /> : "ورود"}
                    </button>

                    {/* <img className='login-logo' src={bistatechLogo} alt="logo" /> */}

                </form>
            </div>
            {/* <button onClick={testCookie}>

                تست کوکی

            </button> */}

        </div>
    )
}

export default Login