import { useState, useEffect, useCallback } from 'react'
import Axios from '../../../config/Axios'
import CheckBoxComponent from '../../../components/Form/CheckBoxComponent'
import InputComponent from '../../../components/Form/InputComponent'
import SelectComponent from '../../../components/Form/SelectComponent'
import Modal from '../../../layouts/Modal/Modal'
import ButtonComponent from '../../../components/Form/ButtonComponent'
import TextAreaComponent from '../../../components/Form/TextAreaComponent'
import useAxios from '../../../middleware/useAxios'
import axiosInstance from '../../../middleware/axiosInstance'
import EachUnitEqulsTo from "../../../components/formForWareHouse/EachUnitEqulsTo/EachUnitEqulsTo"
import Loading from 'layouts/Loading/Loading'

const CreateCommodityAndServices = () => {

  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  const [allCommodityAndServicesGroup, setAllCommodityAndServicesGroup] = useState(null);
  const [newCommodityAndService, setNewCommodityAndService] = useState({});
  const [editeCommodityAndService, setEditeCommodityAndService] = useState({});

  const [secondaryUnits, setSecondaryUnits] = useState([]);


  const [allMeasurementUnits,
    errorGetAllMeasurementUnits,
    loadingGetAllMeasurementUnits,
    refetchAllMeasurementUnits] = useAxios({
      axiosInstance: axiosInstance,
      method: 'GET',
      url: '/v_sanjesh',
      requestConfig: {
        headers: {
          'Content-Type': 'application/json',
          // 'Content-Language': 'en-US',
          //'Accept': 'text/html'
        }
      }
    });
  const [allCommodity,
    errorGetAllCommodity,
    loadingGetAllCommodity,
    refetchAllCommodity] = useAxios({
      axiosInstance: axiosInstance,
      method: 'GET',
      url: '/commodity',
      requestConfig: {
        headers: {
          'Content-Type': 'application/json',

          // 'Content-Language': 'en-US',
          //'Accept': 'text/html'
        }
      }
    });



  const handleInputChange = (e) => {
    const name = e.target.name;
    if (e.target.type === "checkbox") {
      const value = e.target.checked;
      setNewCommodityAndService({ ...newCommodityAndService, [name]: value });
    } else {
      const value = e.target.value;
      setNewCommodityAndService({ ...newCommodityAndService, [name]: value });
    }

  };

  const changeEditeHandler = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setEditeCommodityAndService({ ...setEditeCommodityAndService, [name]: value });
  }

  const createCommodityAndServicesGroupHandler = () => {
    Axios.post("/commodity", newCommodityAndService)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
        setShowAddModal(false)
      })
      .catch((res) => {
        console.log(res);
      })
  }

  const editeCommodityAndServiceHandler = () => {
    Axios.put(`/commodity/${selectedItem.id}`, editeCommodityAndService)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
        setShowEditModal(false)
      })
      .catch((res) => {
        console.log(res);
      })
  }

  const getAllCommodityAndServicesGroup = () => {
    Axios.get("/goods_groups")
      .then((res) => {
        setAllCommodityAndServicesGroup(res.data);
      })
      .catch(() => {
        setAllCommodityAndServicesGroup(false)
        alert("لیست انبارها دریافت نشد")
        console.log("err");
      })
  }

  const deleteCommodityAndServicesGroup = () => {
    Axios.delete(`/commodity/${selectedItem.id}`)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
      })
      .catch((res) => {
        console.log(res);
      })
  }


  useEffect(() => {
    getAllCommodityAndServicesGroup()

  }, []);



  const deleteItemHandler = () => {
    if (window.confirm(`آیا انبار "${selectedItem.name}" حذف گردد؟`)) {
      deleteCommodityAndServicesGroup();
    }
  }



  


  return (
    <div className="customized-container">
      <div className="customized-wrapper">
        {/* {anbar && anbar.map((anbar) => {
          return (
            <h1>
              {anbar.name}
            </h1>
          )
        })} */}

        {showAddModal && <Modal>

          <InputComponent
            inputName="name"
            inputId="name"
            lableValue="نام:"
            onChange={e => handleInputChange(e)}
          />

          <InputComponent
            inputName="name"
            inputId="name"
            lableValue="نام:"
            onChange={e => handleInputChange(e)}
          />

          <div className="d-flex flex-row">
            <ButtonComponent onClick={() => createCommodityAndServicesGroupHandler()} value="ثبت" />
            <ButtonComponent onClick={() => setShowAddModal(false)} value="لغو" />
          </div>

        </Modal>}
        <h5 className="title-session">
          تعریف کالا و خدمات
        </h5>

        {/* <button className='bi-custom-button'
         onClick={() => setShowModal(true)}
        >اضافه کردن</button> */}

        {/* start ttttttaaaaaaaaaaaabbbbbbbbbbbbbblllllllllleeeeeeeeeeee */}
        <div className='d-flex flex-column'>
          < table className='bi-table-style ' >
            <thead className=''>
              <th>
                شناسه
              </th>
              <th>
                نام
              </th>
              <th>
                رهگیری
              </th>
              <th>
                ویژگی
              </th>
              <th>
                قابل مرجوع
              </th>
              <th>
                محاسبه ارزش افزوده در قیمت
              </th>
              <th>
                نوع بارکد
              </th>
              <th>
                بارکد
              </th>
              <th>
                واحد
              </th>
              <th>
                قیمت فروش
              </th>
              <th>
                قیمت خرید
              </th>
              <th>
                نقطه سفارش
              </th>
              <th>
                میزان سفارش
              </th>
              <th>
                قفسه
              </th>
              <th>
                ارزش افزروده
              </th>
              <th>
                مدل
              </th>
              <th>
                سازنده
              </th>
              <th>
                توضیحات
              </th>
              <th>
                ویژگی ها
              </th>

            </thead>
            <tbody>


              {allCommodity && allCommodity[0] ?
                allCommodity.map((commodity, index) => {
                  return (
                    <tr key={index}>
                      <td>
                        {commodity.shenaseh}
                      </td>
                      <td>
                        {commodity.name}

                      </td>
                      <td>
                        {commodity.delivery}
                      </td>
                      <td>
                        {commodity.has_feature}
                      </td>
                      <td>
                        {commodity.returnable}
                      </td>

                      <td>
                        {commodity.mohasebeh}
                      </td>
                      <td>
                        {commodity.barcode_type}
                      </td>
                      <td>
                        {commodity.barcode}
                      </td>
                      <td>
                        {commodity.unit}
                      </td>
                      <td>
                        {commodity.sales_price}
                      </td>
                      <td>
                        {commodity.buy_price}
                      </td>
                      <td>
                        {commodity.order_point}
                      </td>
                      <td>
                        {commodity.order_amount}
                      </td>
                      <td>
                        {commodity.shelf}
                      </td>
                      <td>
                        {commodity.arzesh_afzoode}
                      </td>
                      <td>
                        {commodity.model}
                      </td>
                      <td>
                        {commodity.manufacturer}
                      </td>
                      <td>
                        {commodity.description}
                      </td>
                      <td>
                        {commodity.feature}
                      </td>
                    </tr>
                  )
                })
                :
                loadingGetAllCommodity && <Loading />
              }
            </tbody>

          </table>
        </div>
        {/* end ttttttaaaaaaaaaaaabbbbbbbbbbbbbblllllllllleeeeeeeeeeee */}
        <div className='d-flex flex-column'>

          <SelectComponent
            lableValue="گروه کالا:"
            selectName="goods_group_id"
            selectId="goods_group_id"
            options={allCommodityAndServicesGroup}
            onChange={e => handleInputChange(e)}
          />

          <InputComponent
            lableValue="شناسه:"
            inputName="shenaseh"
            onChange={e => handleInputChange(e)}

          />

          <InputComponent
            lableValue="نام:"
            inputName="name"
            onChange={e => handleInputChange(e)}

          />
          {/* <InputComponent
            lableValue="واحد شمارش اصلی:"
            inputName="unit_id"
            onChange={e => handleInputChange(e)}

          /> */}

          <SelectComponent
            lableValue="واحد شمارش اصلی:"
            selectName="unit_id"
            selectId="unit_id"
            options={allMeasurementUnits}
            onChange={e => handleInputChange(e)}
          />
          <SelectComponent
            lableValue="وضعیت رهگیری:"
            selectName="delivery"
            selectId="delivery"
            options={
              [
                {
                  "name": "بدون رهگیری",
                  "value": "bedon_rahgiri"
                },
                {
                  "name": "سریال",
                  "value": "serial"
                }
              ]
            }
            onChange={e => handleInputChange(e)}
          />
          <CheckBoxComponent
            lableValue="دارای ویژگی می باشد"
            inputName="has_feature"
            onChange={e => handleInputChange(e)}
          />
          <CheckBoxComponent
            lableValue="قابل مرجوع"
            inputName="returnable"
            onChange={e => handleInputChange(e)}
          />
          <SelectComponent
            lableValue="ارزش افزوده:"
            selectName="arzesh_afzoode"
            selectId="arzesh_afzoode"
            options={
              [
                {
                  "name": "مالیات و عوارض",
                  "value": "nadarad"
                },
                {
                  "name": "فاقد ارزش افزوده",
                  "value": "darad"
                }
              ]
            }
            onChange={e => handleInputChange(e)}
          />
          <CheckBoxComponent
            lableValue="محاسبه ارزش افزوده در داخل قیمت"
            inputName="mohasebeh"
            onChange={e => handleInputChange(e)}
          />
          <SelectComponent
            lableValue="نوع بارکد:"
            selectName="barcode_type"
            selectId="barcode_type"
            options={
              [
                {
                  "name": "وزنی",
                  "value": "barcode_weight"
                },
                {
                  "name": "تعدادی",
                  "value": "barcode_number"
                }
              ]
            }
            onChange={e => handleInputChange(e)}
          />
          <InputComponent
            lableValue="بارکد:"
            inputName="barcode"
            onChange={e => handleInputChange(e)}

          />

          <hr />
          <hr />

          <EachUnitEqulsTo
            allMeasurementUnits={allMeasurementUnits}
            secondaryUnits={secondaryUnits}
            setSecondaryUnits={setSecondaryUnits}
          // props={loadingGetAllMeasurementUnits}
          />


          <hr />
          <hr />
          <InputComponent
            lableValue="قیمت خرید:"
            inputName="buy_price"
            onChange={e => handleInputChange(e)}

          />
          <InputComponent
            lableValue="قیمت فروش:"
            inputName="sales_price"
            onChange={e => handleInputChange(e)}

          />
          <InputComponent
            lableValue="نقطه سفارش:"
            inputName="order_point"
            inputType="number"
            onChange={e => handleInputChange(e)}

          />
          <InputComponent
            lableValue="میزان سفارش:"
            inputName="order_amount"
            onChange={e => handleInputChange(e)}

          />
          <InputComponent
            lableValue="قفسه:"
            inputName="shelf"
            onChange={e => handleInputChange(e)}
          />
          <InputComponent
            lableValue="مدل:"
            inputName="model"
            onChange={e => handleInputChange(e)}
          />
          <InputComponent
            lableValue="سازنده:"
            inputName="manufacturer"
            onChange={e => handleInputChange(e)}
          />
          <TextAreaComponent
            lableValue="توضیحات:"
            textareaName="description"
            onChange={e => handleInputChange(e)}

          />

          <p>
            ویژگی ها چگونه وارد شوند؟؟؟
          </p>
          <p>
            هر واحد برابر است با ... چگونه وارد شود؟؟؟
          </p>

          <hr />
          <hr />
          <ButtonComponent onClick={() => createCommodityAndServicesGroupHandler()} value="ثبت" />
        </div>

        {/* <button onClick={(e) => { console.log(e.detail) }}>ssssssssssssssssss</button> */}

      </div>
    </div>
  )
}

export default CreateCommodityAndServices