import React, { useState, useEffect } from 'react'
import "./MeasurementUnit.scss"
import Modal from "../../../layouts/Modal/Modal"
// import InputForm from '../../../InputForm/InputForm';
import { users } from "../../../JsonMockData"
import Axios from "../../../config/Axios"
import Loading from '../../../layouts/Loading/Loading'
import ButtonComponent from '../../../components/Form/ButtonComponent'
import InputComponent from '../../../components/Form/InputComponent'
import TextAreaComponent from '../../../components/Form/TextAreaComponent'
const MeasurementUnit = () => {

  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);

  const [newMeasurement, setNewMeasurement] = useState({})
  const [editeMeasurement, setEditeMeasurement] = useState({})
  const [allCorrentMeasurementUnit, setAllCorrentMeasurementUnit] = useState(null)


  const CreateMeasurementUnitHandler = () => {
    Axios.post("v_sanjesh", newMeasurement)
      .then((res) => {
        console.log(res);
        getAllMeasurementUnit();
        setShowAddModal(false)
      })
      .catch(() => {
        alert("واحد اندازه گیری ایجاد نشد")
      })
  }
  const editeMeasurementUnitHandler = () => {
    Axios.put(`v_sanjesh/${selectedItem.id}`, editeMeasurement)
      .then((res) => {
        console.log(res);
        getAllMeasurementUnit();
        setShowEditModal(false)
      })
      .catch(() => {
        alert("واحد اندازه گیری ایجاد نشد")
      })
  }

  const getAllMeasurementUnit = () => {
    Axios.get("v_sanjesh")
      .then((res) => {
        setAllCorrentMeasurementUnit(res.data)
      })
      .catch(() => {
        alert("لیست واحد های اندازه گیری دریافت نشد")
      })
  }

  const deleteMeasurementUnitHandler = () => {
    Axios.delete(`v_sanjesh/${selectedItem.id}`)
      .then((res) => {
        console.log(res);
        getAllMeasurementUnit();
        setShowAddModal(false)
      })
      .catch(() => {
        alert("واحد اندازه گیری حذف نشد!")
      })
  }


  useEffect(() => {
    getAllMeasurementUnit();
  }, []);


  const handleSelectRow = (item) => {
    setSelectedItem(item)
  }

  // const editItemHandler = () => {
  //   console.log(selectedItem);
  // }

  const deleteItemHandler = () => {
    if (window.confirm(`آیا واحد اندازه گیری "${selectedItem.name}" حذف گردد؟`)) {
      deleteMeasurementUnitHandler();
    }
  }

  const changeInputHandler = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setNewMeasurement({ ...newMeasurement, [name]: value });
  }

  const changeEditeHandler = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setEditeMeasurement({ ...editeMeasurement, [name]: value });
  }






  return (
    <div className="customized-container">
      <div className="customized-wrapper">
        {showAddModal && <Modal >
          {/* <InputForm name button textarea /> */}
          <div className='p-4'>

            <h6 className='text-center mt-2'>
              ایجاد یک واحد اندازه گیری:
            </h6>
            <InputComponent
              lableValue="نام:"
              inputType="text"
              onChange={changeInputHandler}
              inputName="name"
            />
            <InputComponent
              lableValue="تعداد اعشار:"
              inputType="number"
              onChange={changeInputHandler}
              inputName="tedad_ashar"
            />
            {/* <TextAreaComponent
              lableValue="توضیحات:"
              onChange={changeInputHandler}
              textareaName="description"
            /> */}
            <div className='d-flex justify-content-evenly  flex-wrap mt-4'>

              <ButtonComponent value="ثبت" onClick={() => CreateMeasurementUnitHandler()} />
              <ButtonComponent value="انصراف" onClick={() => setShowAddModal(false)} />
            </div>
          </div>
        </Modal>}

        {showEditModal && <Modal >
          <div className='p-4'>

            <h6 className='text-center mt-2'>
              ویرایش واحد اندازه گیری

              {` ${selectedItem.name}: `}
            </h6>
            <InputComponent
              lableValue="نام:"
              inputType="text"
              onChange={changeEditeHandler}
              inputName="name"
              defaultValue={selectedItem.name}
            />
            <InputComponent
              lableValue="تعداد اعشار:"
              inputType="number"
              onChange={changeEditeHandler}
              inputName="tedad_ashar"
              defaultValue={selectedItem.tedad_ashar}
            />
            {/* <TextAreaComponent
            lableValue="توضیحات:"
            onChange={changeEditeHandler}
            defaultValue={selectedItem.description}
          /> */}
            <div className='d-flex justify-content-evenly  flex-wrap mt-4'>
              <ButtonComponent value="ثبت" onClick={() => editeMeasurementUnitHandler()} />
              <ButtonComponent value="انصراف" onClick={() => setShowEditModal(false)} />
            </div>
          </div>
        </Modal>}

        <h5 className="title-session">
          واحدهای اندازه گیری
        </h5>



        <div className='d-flex flex-row '>

          <button className='bi-custom-button'
            onClick={() => setShowAddModal(true)}
          >اضافه کردن</button>

          <button className='bi-custom-button'
            onClick={() => selectedItem ? setShowEditModal(true) : alert("جهت ویرایش  یک آیتم را انتخاب کنید!")}
          >ویرایش</button>
          <button className='bi-custom-button'
            onClick={() => selectedItem ? deleteItemHandler() : alert("جهت حذف یک آیتم را انتخاب کنید!")}
          >حذف</button>
        </div>
        <>
          {allCorrentMeasurementUnit === null ?
            <Loading />
            :
            // allCorrentMeasurementUnit.length === 0 ?

            //   <h1>
            //     واحد اندازه گیری تعریف نشده است
            //   </h1>
            //   :
            <table className="bi-table-style bi-row-select-enable col-12 col-md-6 col-lg-4">
              <thead>
                <tr>
                  <th >ردیف</th>
                  <th >نام</th>
                  <th >تعداد اعشار</th>
                  {/* <th >توضیحات</th> */}
                </tr>
              </thead>
              <tbody>
                {allCorrentMeasurementUnit.map((measurementUnit, index) => {
                  return (
                    <tr id={measurementUnit.id}
                      onClick={() => handleSelectRow(measurementUnit)}
                      className={selectedItem ? measurementUnit.id === selectedItem.id ? "item-slected" : null : null}>
                      <td >
                        {index + 1}
                        {/* {measurementUnit.code} */}
                      </td>
                      <td>
                        {measurementUnit.name}
                      </td>
                      <td >
                        {measurementUnit.tedad_ashar}
                      </td>
                      {/* <td >
                        {measurementUnit.description}
                      </td> */}
                    </tr>
                  )
                })}
              </tbody>
            </table>
          }
        </>
      </div>
    </div>
  )
}
export default MeasurementUnit