import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";

import NavbarAndSidebar from "pages/NavbarAndSidebar";
import CopyRightMessage from "layouts/CopyRightMessage/CopyRightMessage"

import MeasurementUnit from "pages/warehouse/MeasurementUnit/MeasurementUnit";
import CommodityAndServicesGroup from "pages/warehouse/CommodityAndServicesGroup/CommodityAndServicesGroup";
import CreateWarehouse from "pages/warehouse/CreateWarehouse/CreateWarehouse";
import CreateCommodityAndServices from "pages/warehouse/CreateCommodityAndServices/CreateCommodityAndServices";
import FrequentlyServices from "pages/FrequentlyServices/FrequentlyServices";
import WarehouseEntryDocument from "pages/warehouse/WarehouseEntryDocument/WarehouseEntryDocument";
import WarehouseExitDocument from "pages/warehouse/WarehouseExitDocument/WarehouseExitDocument";
import WarehouseOpeningDocuments from "pages/warehouse/WarehouseOpeningDocuments/WarehouseOpeningDocuments";
import WarehouseClosingDocuments from "pages/warehouse/WarehouseClosingDocuments/WarehouseClosingDocuments";
import WarehouseHandling from "pages/warehouse/WarehouseHandling/WarehouseHandling";
import Login from "pages/Login/Login";
import Persons from "pages/warehouse/Persons/Persons";
import CreateWarehouseEntryDocument from "pages/warehouse/CreateWarehouseEntryDocument/CreateWarehouseEntryDocument";
import CreateWarehouseEntryDocumentWithAntd from "componentForTest/CreateWarehouseEntryDocumentWithAntd";
import WarehouseEntryDocumentAntd from "componentForTest/WarehouseEntryDocumentAntd";


const ProjectRoutes = () => {
  return (
    <Router>
      
      <NavbarAndSidebar />
      <Routes>
        <Route index element={<FrequentlyServices />} />
        <Route path="/warehouse/measurement-unit" element={<MeasurementUnit />} />
        <Route path="/warehouse/commodity-and-services-group" element={<CommodityAndServicesGroup />} />
        <Route path="/warehouse/create-warehouse" element={<CreateWarehouse />} />
        <Route path="/warehouse/create-commodity-and-services" element={<CreateCommodityAndServices />} />
        <Route path="/warehouse/warehouse-entry-document" element={<WarehouseEntryDocument />} />
        <Route path="/warehouse/warehouse-entry-document-antd" element={<WarehouseEntryDocumentAntd />} />
        <Route path="/warehouse/create-warehouse-entry-document" element={<CreateWarehouseEntryDocument />} />
        <Route path="/warehouse/create-warehouse-entry-document-antd" element={<CreateWarehouseEntryDocumentWithAntd />} />
        <Route path="/warehouse/warehouse-exit-document" element={<WarehouseExitDocument />} />
        <Route path="/warehouse/warehouse-opening-documents" element={<WarehouseOpeningDocuments />} />
        <Route path="/warehouse/warehouse-closing-documents" element={<WarehouseClosingDocuments />} />
        <Route path="/warehouse/warehouse-handling" element={<WarehouseHandling />} />
        <Route path="/warehouse/persons" element={<Persons />} />


        {/* <Route index element={<Home />} />
                    <Route path=":teamId" element={<Team />} />
                    <Route path="new" element={<NewTeamForm />} />
                    <Route index element={<LeagueStandings />} />
                */}
        {/* <Route path="/router-board/status" element={<RouterBoardStatus />} /> */}

        <Route path="/login" element={<Login />} />

        {/* notfound page */}
        <Route path="*" element={<FrequentlyServices />} />
      </Routes>
      <CopyRightMessage />
    </Router>
  )
}

export default ProjectRoutes