import { Button, Checkbox, Form, Input } from "antd";

const CreateWarehouseEntryDocumentWithAntd = () => {
  return (
    <div className="customized-container">
      <div className="customized-wrapper">
        <h5 className="title-session">سندهای ورودی انبار</h5>{" "}
        <Form
          name="basic"
          labelCol={{
            span: 2,
          }}
          labelWrap="true"
          wrapperCol={{
            span: 15,
          }}
          initialValues={{
            remember: true,
          }}
          // onFinish={onFinish}
          // onFinishFailed={onFinishFailed}
          autoComplete="off"
        >
          <Form.Item
            label="Udsadasdasd vdd d sdsername"
            name="username"
            rules={[
              {
                required: true,
                message: "Please input your username!",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label="Password"
            name="password"
            rules={[
              {
                required: true,
                message: "Please input your password!",
              },
            ]}
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            name="remember"
            valuePropName="checked"
            wrapperCol={{
              offset: 8,
              span: 16,
            }}
          >
            <Checkbox>Remember me</Checkbox>
          </Form.Item>

          <Form.Item
            wrapperCol={{
              offset: 8,
              span: 16,
            }}
          >
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default CreateWarehouseEntryDocumentWithAntd;

