import { useState, useEffect } from 'react'
import axiosInstance from 'middleware/axiosInstance'
import useAxios from 'middleware/useAxios'

const WarehouseEntryDocumentAntd = () => {
  const [
    allWarehouseEntryDocument,
    errorGetAllWarehouseEntryDocument,
    loadingGetAllWarehouseEntryDocument,
    refetchAllWarehouseEntryDocument,
  ] = useAxios({
    axiosInstance: axiosInstance,
    method: "GET",
    url: "/import",
    requestConfig: {
      headers: {
        "Content-Type": "application/json",
        //   'Content-Language': 'en-US',
        // 'Accept': 'text/html'
      },
    },
  })

  return (
    <div className="customized-container">
      <div className="customized-wrapper">
        <h5 className="title-session">سند ورودی انبار</h5>
        {allWarehouseEntryDocument &&
          <table className='bi-table-style '>
            <thead>
              <tr>
                <th>
                  ردیف
                </th>
                <th>
                  شماره سند
                </th>
                <th>
                  نوع سند
                </th>
                <th>
                  تاریخ
                </th>
                <th>
                  نام شخص اول
                </th>
                <th>
                  نام شخص دوم
                </th>
                <th>
                  انبار
                </th>
                <th>
                  تعداد اقلام
                </th>
                <th>
                  توضیحات
                </th>
              </tr>
            </thead>
            <tbody>
              {allWarehouseEntryDocument?.map((document, index) => {
                return (
                  <tr>
                    <td>
                      {index + 1}
                    </td>
                    <td>
                      {document.sh_sanad}
                    </td>
                    <td>
                    </td>
                    <td>
                      {document.date}
                    </td>
                    <td>
                      {`${document.name} ${document.family}`}
                    </td>
                    <td>
                      {document.p2_name ? `${document.p2_name} + ${document.p2_family}` : null}
                    </td>
                    <td>
                      {document.anbar_name}
                    </td>
                    <td>
                      {document.item.length}
                    </td>
                    <td>
                      {document.description}
                    </td>
                  </tr>
                )
              })}
            </tbody>
          </table>
        }
      </div>
    </div>
  )
}

export default WarehouseEntryDocumentAntd