import TableComponent from 'componentForTest/Table';
import { useState, useEffect } from 'react'
import ButtonComponent from '../../../components/Form/ButtonComponent';
import InputComponent from '../../../components/Form/InputComponent';
import SelectComponent from '../../../components/Form/SelectComponent';
import TextAreaComponent from '../../../components/Form/TextAreaComponent';
import Axios from '../../../config/Axios';
import Loading from '../../../layouts/Loading/Loading';
import Modal from '../../../layouts/Modal/Modal';



const Persons = () => {

    // test
    const [file, setFile] = useState({ base64: "" });
    const getBase64 = file => {
        return new Promise(resolve => {
            let fileInfo;
            let baseURL = "";
            // Make new FileReader
            let reader = new FileReader();

            // Convert the file to base64 text
            reader.readAsDataURL(file);

            // on reader load somthing...
            reader.onload = () => {
                // Make a fileInfo Object
                console.log("Called", reader);
                baseURL = reader.result;
                console.log(baseURL);
                resolve(baseURL);
            };
            console.log(fileInfo);
        });
    };

    const handleFileInputChange = e => {
        console.log(e.target.files[0]);

        setFile(e.target.files[0])
        file = e.target.files[0]
        getBase64(file)
            .then(result => {
                file["base64"] = result;
                console.log("File Is", file);
                setFile(file)
            })
            .catch(err => {
                console.log(err);
            });

        setFile(e.target.files[0])
        setNewPerson({ ...newPerson, "image": file.base64, image_type: file.type.split("/")[1] })
        // console.log(file.type.split("/")[1]);
        // console.log(file.type);
    };

    //test



    const [showAddModal, setShowAddModal] = useState(false);
    const [showEditModal, setShowEditModal] = useState(false);
    const [selectedItem, setSelectedItem] = useState(null);
    const [allPersons, setAllPersons] = useState(null);
    const [newPerson, setNewPerson] = useState({});
    const [editePerson, setEditePerson] = useState({});
    const [peopleGroup, setPeopleGroup] = useState()
    const [allProvinceAndCities, setAllProvinceAndCities] = useState()
    const [citiesOfSelectedProvince, setCitiesOfSelectedProvince] = useState(null)


    const handleInputChange = (e) => {
        const value = e.target.value;
        const name = e.target.name;
        setNewPerson({ ...newPerson, [name]: value });
    };

    const changeEditeHandler = (e) => {
        const value = e.target.value;
        const name = e.target.name;
        setEditePerson({ ...editePerson, [name]: value });
    }


    const CreatePersonHandler = () => {
        Axios.post("/people", newPerson,
            {
                headers: {
                    // 'Content-Type': 'application/json',

                    "content-type": 'multipart/form-data'
                }
            }
        )
            .then((res) => {
                console.log(res);
                getAllPersons();
                setShowAddModal(false)
            })
            .catch((res) => {
                console.log(res);
            })
    }
    const editePersonHandler = () => {
        Axios.put(`/people/${selectedItem.id}`, editePerson, {
            headers: {
                'Content-Type': 'application/json',
            }
        })
            .then((res) => {
                getAllPersons();
                setShowEditModal(false)
            })
            .catch((err) => {
                console.log(err);
            })
    }

    const getAllPersons = () => {
        Axios.get("/people")
            .then((res) => {
                setAllPersons(res.data);
            })
            .catch(() => {
                setAllPersons(false)
                alert("لیست انبارها دریافت نشد")
                console.log("err");
            })
    }

    const deletePersonHandler = () => {
        Axios.delete(`/people/${selectedItem.id}`, newPerson)
            .then((res) => {
                console.log(res);
                getAllPersons();
                // setShowAddModal(false)
            })
            .catch((err) => {
                console.log(err);
            })
    }

    const getPeopleGroup = () => {
        Axios.get("/g_people")
            .then((res) => {
                setPeopleGroup(res.data);
            })
            .catch((err) => {
                setPeopleGroup(false)
                alert("گروه اشخاص دریافت نشد")
                console.log(err);
            })
    }
    const deleteItemHandler = () => {
        if (window.confirm(`آیا انبار "${selectedItem.name}" حذف گردد؟`)) {
            deletePersonHandler();
        }
    }

    const getAndSetAllprovince = () => {
        Axios.get("/province")
            .then((res) => {
                console.log(res.data);
                setAllProvinceAndCities(res.data)
            })
            .catch((err) => {
                console.log(err)
            })
    }
    const getAndSetAllCitiesOfSelectedProvince = (idOfSelectedProvince) => {

        Axios.get(`/province?id=${idOfSelectedProvince}`)
            .then((res) => {
                console.log(res.data);
                setCitiesOfSelectedProvince(res.data[0].city)
            })
            .catch((err) => {
                console.log(err)
            })

    }

    useEffect(() => {
        getAllPersons()
        getPeopleGroup()
        getAndSetAllprovince()
    }, []);



    const handleSelectRow = (item) => {
        setSelectedItem(item)
    }


    const handleSelectProvince = (e) => {
        handleInputChange(e);
        const idOfSelectedProvince = e.target.value
        getAndSetAllCitiesOfSelectedProvince(idOfSelectedProvince)
        console.log(e);
    }

    return (

        <div className="customized-container">
            <div className="customized-wrapper">
                {showAddModal && <Modal>
                    <div className='d-flex flex-row flex-wrap'>

                        {/* <InputComponent
                            inputName="people_id"
                            inputId="people_id"
                            lableValue="شناسه:"
                            onChange={e => handleInputChange(e)}
                        /> */}

                        <SelectComponent
                            lableValue="شناسه:"
                            selectName="people_id"
                            selectId="people_id"
                            options={peopleGroup}
                            onChange={e => handleInputChange(e)}
                        />

                        <InputComponent
                            inputName="image"
                            inputId="image"
                            lableValue="تصویر:"
                            onChange={e => handleFileInputChange(e)}
                            inputType="file"
                        />
                        <SelectComponent
                            lableValue="نوع شخص:"
                            selectName="type_person"
                            selectId="type_person"
                            options={
                                [
                                    {
                                        "name": "حقیقی",
                                        "value": "genuine"
                                    },
                                    {
                                        "name": "حقوقی",
                                        "value": "legal"
                                    }
                                ]
                            }
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="code"
                            inputId="code"
                            lableValue="کد:"
                            inputPlaceholder="123"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="name"
                            inputId="name"
                            lableValue="نام:"
                            inputPlaceholder="احمد"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="family"
                            inputId="family"
                            lableValue="نام خانوادگی:"
                            inputPlaceholder="محمدی"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="national_code"
                            inputId="national_code"
                            lableValue="کدملی:"
                            inputPlaceholder="1234567890"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="shenaseh_melli"
                            inputId="shenaseh_melli"
                            lableValue="شناسه ملی:"
                            inputPlaceholder="9876543210"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="company_name"
                            inputId="company_name"
                            lableValue="نام شرکت:"
                            inputPlaceholder="دیجی کالا"
                            onChange={e => handleInputChange(e)}
                            errorMessage="این پیام خطا تست است"
                        />
                        <InputComponent
                            inputName="birth_day"
                            inputId="birth_day"
                            lableValue="تاریخ تولد:"
                            inputPlaceholder="1401/01/01"
                            onChange={e => handleInputChange(e)}
                            inputType="date"
                        />
                        <InputComponent
                            inputName="economic_code"
                            inputId="economic_code"
                            lableValue="کد اقتصادی:"
                            inputPlaceholder="852357951"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="cellular_phone"
                            inputId="cellular_phone"
                            lableValue="تلفن همراه:"
                            inputPlaceholder="09123456789"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="phone"
                            inputId="phone"
                            lableValue="تلفن:"
                            inputPlaceholder="02132659874"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="email"
                            inputId="email"
                            lableValue="آدرس ایمیل:"
                            inputPlaceholder="test@test.com"
                            onChange={e => handleInputChange(e)}
                        />
                        <InputComponent
                            inputName="postal_code"
                            inputId="postal_code"
                            lableValue="کد پستی:"
                            inputPlaceholder="12345-67890"
                            onChange={e => handleInputChange(e)}
                        />
                        {/* <InputComponent
                            inputName="province_id"
                            inputId="province_id"
                            lableValue="استان:"
                            onChange={e => handleInputChange(e)}
                        /> */}
                        <SelectComponent
                            lableValue="استان:"
                            selectName="province_id"
                            selectId="province_id"
                            onChange={e => handleSelectProvince(e)}
                            // inputPlaceholder="12345-67890"
                            options={allProvinceAndCities}
                        />
                        <SelectComponent
                            lableValue="شهر:"
                            selectName="city_id"
                            selectId="city_id"
                            onChange={e => handleInputChange(e)}
                            optionHiddenText={
                                citiesOfSelectedProvince ? "ابتدا استان را انتخاب فرمایید" : null}
                            options={
                                citiesOfSelectedProvince ? citiesOfSelectedProvince : null
                                // [{
                                //     "name": "حقیقی",
                                //     "value": "genuine"
                                // }]
                            }

                        />
                        <TextAreaComponent
                            lableValue="آدرس:"
                            textareaName="address"
                            textareaId="address"
                            onChange={e => handleInputChange(e)}
                        />
                        <SelectComponent
                            lableValue="مراودات:"
                            selectName="moravedat"
                            selectId="moravedat"
                            options={
                                [
                                    {
                                        "name": "مشتری",
                                        "value": "moshtari",
                                    },
                                    {
                                        "name": "تامین کننده",
                                        "value": "tamin_konande"
                                    },
                                    {
                                        "name": "کارکنان",
                                        "value": "karkonan"
                                    },
                                    {
                                        "name": "واسطه",
                                        "value": "vaseteh"
                                    }
                                ]
                            }
                            onChange={e => handleInputChange(e)}
                        />
                        <div className="d-flex flex-row">
                            <ButtonComponent onClick={() => CreatePersonHandler()} value="ثبت" />
                            <ButtonComponent onClick={() => setShowAddModal(false)} value="لغو" />
                        </div>
                    </div>

                </Modal>}
                {showEditModal && <Modal>
                    <div className='d-flex flex-row flex-wrap'>

                        {/* <InputComponent
                            inputName="people_id"
                            inputId="people_id"
                            lableValue="شناسه:"
                            onChange={e => handleInputChange(e)}
                        /> */}

                        <SelectComponent
                            lableValue="شناسه:"
                            selectName="people_id"
                            selectId="people_id"
                            options={peopleGroup}
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.people_id}
                        />

                        <InputComponent
                            inputName="image"
                            inputId="image"
                            lableValue="تصویر:"
                            onChange={e => handleFileInputChange(e)}
                            inputType="file"
                        />
                        <SelectComponent
                            lableValue="نوع شخص:"
                            selectName="type_person"
                            selectId="type_person"
                            options={
                                [
                                    {
                                        "name": "حقیقی",
                                        "value": "genuine"
                                    },
                                    {
                                        "name": "حقوقی",
                                        "value": "legal"
                                    }
                                ]
                            }
                            onChange={e => changeEditeHandler(e)}
                            defaultOptionSelected={selectedItem.type_person}
                        />
                        <InputComponent
                            inputName="code"
                            inputId="code"
                            lableValue="کد:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.code}

                        />
                        <InputComponent
                            inputName="name"
                            inputId="name"
                            lableValue="نام:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.name}

                        />
                        <InputComponent
                            inputName="family"
                            inputId="family"
                            lableValue="نام خانوادگی:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.family}

                        />
                        <InputComponent
                            inputName="national_code"
                            inputId="national_code"
                            lableValue="کدملی:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.national_code}

                        />
                        <InputComponent
                            inputName="shenaseh_melli"
                            inputId="shenaseh_melli"
                            lableValue="شناسه ملی:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.shenaseh_melli}

                        />
                        <InputComponent
                            inputName="company_name"
                            inputId="company_name"
                            lableValue="نام شرکت:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.company_name}

                        />
                        <InputComponent
                            inputName="birth_day"
                            inputId="birth_day"
                            lableValue="تاریخ تولد:"
                            onChange={e => changeEditeHandler(e)}
                            inputType="date"
                            defaultValue={selectedItem.birth_day}

                        />
                        <InputComponent
                            inputName="economic_code"
                            inputId="economic_code"
                            lableValue="کد اقتصادی:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.economic_code}

                        />
                        <InputComponent
                            inputName="cellular_phone"
                            inputId="cellular_phone"
                            lableValue="تلفن همراه:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.cellular_phone}

                        />
                        <InputComponent
                            inputName="phone"
                            inputId="phone"
                            lableValue="تلفن:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.phone}

                        />
                        <InputComponent
                            inputName="email"
                            inputId="email"
                            lableValue="آدرس ایمیل:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.email}

                        />
                        <InputComponent
                            inputName="postal_code"
                            inputId="postal_code"
                            lableValue="کد پستی:"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.postal_code}

                        />
                        {/* <InputComponent
                            inputName="province_id"
                            inputId="province_id"
                            lableValue="استان:"
                            onChange={e => changeEditeHandler(e)}
                        /> */}
                        <SelectComponent
                            lableValue="استان:"
                            selectName="province_id"
                            selectId="province_id"
                            onChange={e => handleSelectProvince(e)}
                            options={allProvinceAndCities}
                            defaultValue={selectedItem.province_id}

                        />
                        <SelectComponent
                            lableValue="شهر:"
                            selectName="city_id"
                            selectId="city_id"
                            onChange={e => changeEditeHandler(e)}
                            optionHiddenText={
                                citiesOfSelectedProvince ? "ابتدا استان را انتخاب فرمایید" : null}
                            options={
                                citiesOfSelectedProvince ? citiesOfSelectedProvince : null
                                // [{
                                //     "name": "حقیقی",
                                //     "value": "genuine"
                                // }]
                            }
                            defaultValue={selectedItem.city_id}


                        />
                        <TextAreaComponent
                            lableValue="آدرس:"
                            textareaName="address"
                            textareaId="address"
                            onChange={e => changeEditeHandler(e)}
                            defaultValue={selectedItem.address}

                        />
                        <SelectComponent
                            lableValue="مراودات:"
                            selectName="moravedat"
                            selectId="moravedat"
                            options={
                                [
                                    {
                                        "name": "مشتری",
                                        "value": "moshtari",
                                    },
                                    {
                                        "name": "تامین کننده",
                                        "value": "tamin_konande"
                                    },
                                    {
                                        "name": "کارکنان",
                                        "value": "karkonan"
                                    },
                                    {
                                        "name": "واسطه",
                                        "value": "vaseteh"
                                    }
                                ]
                            }
                            onChange={e => changeEditeHandler(e)}
                            defaultOptionSelected={selectedItem.moravedat}

                        />
                        <div className="d-flex flex-row">
                            <ButtonComponent onClick={() => editePersonHandler()} value="ثبت" />
                            <ButtonComponent onClick={() => setShowEditModal(false)} value="لغو" />
                        </div>
                    </div>

                </Modal>}
                <h5 className="title-session">
                    اشخاص
                </h5>

                <div className='d-flex flex-row '>
                    <button className='bi-custom-button'
                        onClick={() => setShowAddModal(true)}
                    >اضافه کردن</button>
                    <button className='bi-custom-button'
                        onClick={() => selectedItem ? setShowEditModal(true) : alert("جهت ویرایش  یک آیتم را انتخاب کنید!")}
                    >ویرایش</button>
                    <button className='bi-custom-button'
                        onClick={() => selectedItem ? deleteItemHandler() : alert("جهت حذف یک آیتم را انتخاب کنید!")}
                    >حذف</button>
                </div>
                {
                    allPersons === null ? <Loading /> :
                        allPersons.length > 0 ?
                            < table className="bi-table-style">
                                <thead>
                                    <tr>
                                        <th >آیدی</th>
                                        <th >نام</th>
                                        <th >نام خانوادگی</th>
                                        <th >نوع</th>
                                        <th >توضیحات</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        allPersons.map((person) => {
                                            return (
                                                <tr id={person.id}
                                                    onClick={() => handleSelectRow(person)}
                                                    className={selectedItem ? person.id === selectedItem.id ? "item-slected" : null : null}>
                                                    <td >
                                                        {person.id}
                                                    </td>

                                                    <td >
                                                        {person.name}
                                                    </td>

                                                    <td >
                                                        {person.family}
                                                    </td>

                                                    <td >
                                                        {
                                                            person.type_person === "genuine" ?
                                                                "حقیقی" :
                                                                "حقوقی"
                                                        }
                                                    </td>

                                                    <td >
                                                        {person.description}
                                                    </td>

                                                </tr>

                                            )
                                        })
                                    }

                                </tbody>
                            </table>
                            :
                            <h1>
                                کاربری تعریف نشده
                            </h1>
                }
                <TableComponent />
            </div>
        </div>



    )
}

export default Persons