import React, { useState, useEffect, useCallback } from "react";
import InputComponent from "components/Form/InputComponent";
import ButtonComponent from "components/Form/ButtonComponent";
import TextAreaComponent from "components/Form/TextAreaComponent";
import Modal from "layouts/Modal/Modal";
import SelectComponent from "components/Form/SelectComponent";
import useAxios from "middleware/useAxios";
import Axios from "config/Axios";
import CheckBoxComponent from "components/Form/CheckBoxComponent";
import axiosInstance from "middleware/axiosInstance";
import addPlusIcon from "assets/images/wareHouse-icons/add-plus.svg";
import InputDynamicRowForEntryDocument from "components/formForWareHouse/InputDynamicRowForEntryDocument/InputDynamicRowForEntryDocument";
import EntryDocumentFormValidation from "utils/validations/warehouseForms/EntryDocumentValidation";

const CreateWarehouseEntryDocument = () => {
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  const [allWarehouseEntryDocument, setAllWarehouseEntryDocument] =
    useState(null);
  const [newWarehouseEntryDocument, setNewWarehouseEntryDocument] = useState(
    {}
  );
  const [editeWarehouseEntryDocument, setEditeWarehouseEntryDocument] =
    useState({});
  const [selectedRowInTable, setSelectedRowInTable] = useState(0);

  const [error, setError] = useState();

  const [
    allGoodsGroup,
    errorGetAllGoodsGroup,
    loadingGetAllGoodsGroup,
    refetchAllGoodsGroup,
  ] = useAxios({
    axiosInstance: axiosInstance,
    method: "GET",
    url: "/goods_groups",
    requestConfig: {
      headers: {
        "Content-Type": "application/json",
        //   'Content-Language': 'en-US',
        // 'Accept': 'text/html'
      },
    },
  });
  const [allPeople,
    errorGetAllPeople,
    loadingGetAllPeople,
    refetchAllPeople] =
    useAxios({
      axiosInstance: axiosInstance,
      method: "GET",
      url: "/people",
      requestConfig: {
        // headers: {
        //   'Content-Language': 'en-US',
        //   'Accept': 'text/html'
        // }
      },
    });

  const handleInputChange = (e) => {
    const name = e.target.name;
    if (e.target.type === "checkbox") {
      const value = e.target.checked;
      setNewWarehouseEntryDocument({
        ...newWarehouseEntryDocument,
        [name]: value,
      });
    } else {
      const value = e.target.value;
      setNewWarehouseEntryDocument({
        ...newWarehouseEntryDocument,
        [name]: value,
      });
    }
  };

  const changeEditeHandler = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setEditeWarehouseEntryDocument({
      ...setEditeWarehouseEntryDocument,
      [name]: value,
    });
  };

  const createCommodityAndServicesGroupHandler = async () => {
    const mixNewWarehouseEntryDocumentWithCommodities = await {
      ...newWarehouseEntryDocument,
      items: inputFields,
    };
    EntryDocumentFormValidation(
      setError,
      mixNewWarehouseEntryDocumentWithCommodities
    );
    Axios.post("/import", mixNewWarehouseEntryDocumentWithCommodities)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
        setShowAddModal(false);
      })
      .catch((res) => {
        console.log(res);
      });
  };

  const editeWarehouseEntryDocumentHandler = () => {
    Axios.put(`/import/${selectedItem.id}`, editeWarehouseEntryDocument)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
        setShowEditModal(false);
      })
      .catch((res) => {
        console.log(res);
      });
  };

  const getAllCommodityAndServicesGroup = () => {
    Axios.get("/goods_groups")
      .then((res) => {
        setAllWarehouseEntryDocument(res.data);
      })
      .catch(() => {
        setAllWarehouseEntryDocument(false);
        alert("لیست انبارها دریافت نشد");
        console.log("err");
      });
  };

  const deleteCommodityAndServicesGroup = () => {
    Axios.delete(`/import/${selectedItem.id}`)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
      })
      .catch((res) => {
        console.log(res);
      });
  };

  useEffect(() => {
    getAllCommodityAndServicesGroup();
  }, []);



  // test start
  const handleKeyPress = useCallback((event) => {
    if (event.ctrlKey === true && event.key === "Insert") {
      // console.log(`Key pressed: ${event.key}`);
      addFields()
    }
  }, []);

  useEffect(() => {
    // attach the event listener
    document.addEventListener('keydown', handleKeyPress);

    // remove the event listener
    return () => {
      document.removeEventListener('keydown', handleKeyPress);
    };
  }, [handleKeyPress]);
  // test end



  const deleteItemHandler = () => {
    if (window.confirm(`آیا انبار "${selectedItem.name}" حذف گردد؟`)) {
      deleteCommodityAndServicesGroup();
    }
  };

  // start dynamic form fields row

  const [inputFields, setInputFields] = useState([{ name: "" }]);

  const addFields = () => {
    let newfield = { name: "" };
    setInputFields([...inputFields, newfield]);
    setSelectedRowInTable(inputFields.length)
  };

  const removeFields = (index) => {
    let data = [...inputFields];
    data.splice(index, 1);
    setInputFields(data);
  };

  const handleInputChangeCommodityRowInput = (index, event) => {
    let data = [...inputFields];
    data[index][event.target.name] = event.target.value;
    setInputFields(data);
  };
  const handleInputChangeCommodityRowOptions = (index, event) => {
    let data = [...inputFields];
    data[index][`shenaseh`] = JSON.parse(event.target.value).shenaseh;
    data[index][`name`] = JSON.parse(event.target.value).name;
    data[index][`unit`] = JSON.parse(event.target.value).unit;
    data[index][`commodity_id`] = JSON.parse(event.target.value).id;
    setInputFields(data);
  };

  const submit = (e) => {
    e.preventDefault();
    console.log(inputFields);
  };

  // end dynamic form fields row

  return (
    <div className="customized-container">
      <div className="customized-wrapper">
        <h5 className="title-session">سند ورودی انبار</h5>
        {/* {error?.map((err) => {
          return <h1>{err.nameError}</h1>;
        })} */}
        {/* <button
          className="bi-custom-button"
          onClick={() => setShowAddModal(true)}
        >
          اضافه کردن
        </button> */}



        <form>
          <SelectComponent
            lableValue="انبار ورود:"
            selectName="anbar_id"
            selectId="anbar_id"
            options={allGoodsGroup}
            onChange={(e) => handleInputChange(e)}
          />
          <InputComponent
            inputName="sh_sanad"
            inputId="sh_sanad"
            lableValue="شماره سند:"
            onChange={(e) => handleInputChange(e)}
            error={error?.nameError}
          />

          <InputComponent
            inputName="sh_atf"
            inputId="sh_atf"
            lableValue="شماره عطف:"
            onChange={(e) => handleInputChange(e)}
          />

          {/* <TimePicker
            clearable
            okLabel="تأیید"
            cancelLabel="لغو"
            clearLabel="پاک کردن"
            label="ساعت شروع مرخصی"
            value={StartTime}
            onChange={handleStartDateChange}
            ampm={false}
            fullWidth
            minutesStep={15}
            onKeyDown={(event) => {
              event.preventDefault();
              return false
            }}
          /> */}
          <InputComponent
            inputName="time"
            inputId="time"
            lableValue="ساعت:"
            onChange={(e) => handleInputChange(e)}
            inputType="time"
          />
          <InputComponent
            inputName="date"
            inputId="date"
            lableValue="تاریخ:"
            onChange={(e) => handleInputChange(e)}
            inputType="date"
          />

          <label htmlFor="people_id">شخص:</label>
          <select
            name="people_id"
            id="people_id"
            onChange={(e) => handleInputChange(e)}
          >
            <option hidden>
              {allPeople ? "انتخاب کنید" : "درحال بارگذاری ..."}
            </option>
            {allPeople &&
              allPeople.map((people) => {
                return (
                  <option
                    key={people.id}
                    value={people.id}
                  >{`${people.name} ${people.family}`}</option>
                );
              })}
          </select>
          {/* <SelectComponent
            lableValue="شخص:"
            selectName="people_id"
            selectId="people_id"
            options={allPeople}
            onChange={e => handleInputChange(e)}
          /> */}

          <InputComponent
            inputName="people_label"
            inputId="people_label"
            lableValue="عنوان شخص:"
            onChange={(e) => handleInputChange(e)}
          />
          {/* <SelectComponent
            lableValue="شخص دوم:"
            selectName="people2_id"
            selectId="people2_id"
            options={allGoodsGroup}
            onChange={e => handleInputChange(e)}
          /> */}
          <label htmlFor="people2_id">شخص دوم:</label>
          <select
            name="people2_id"
            id="people2_id"
            onChange={(e) => handleInputChange(e)}
          >
            <option hidden>
              {allPeople ? "انتخاب کنید" : "درحال بارگذاری ..."}
            </option>
            {allPeople &&
              allPeople.map((people) => {
                return (
                  <option
                    key={people.id}
                    value={people.id}
                  >{`${people.name} ${people.family}`}</option>
                );
              })}
          </select>
          <SelectComponent
            lableValue="ارز:"
            selectName="arz"
            selectId="arz"
            options={[
              {
                name: "ریال",
                value: "rial",
              },
            ]}
            onChange={(e) => handleInputChange(e)}
          />

          <InputComponent
            inputName="nerkh_arz"
            inputId="nerkh_arz"
            lableValue="نرخ ارز:"
            inputType="number"
            onChange={(e) => handleInputChange(e)}
          />

          <TextAreaComponent
            lableValue="شرح:"
            textareaName="description"
            textareaId="description"
            onChange={(e) => handleInputChange(e)}
          />

          {/* ***************************

          <InputComponent
            inputName="name"
            inputId="name"
            lableValue="نام:"
            onChange={e => handleInputChange(e)}
          />
          <InputComponent
            inputName="unit_id"
            inputId="unit_id"
            lableValue="واحد اندازه گیری:"
            onChange={e => handleInputChange(e)}
          />
          <InputComponent
            inputName="name"
            inputId="name"
            lableValue="نام:"
            onChange={e => handleInputChange(e)}
          />
          <SelectComponent
            lableValue="گروه کالا:"
            selectName="goods_group_id"
            selectId="goods_group_id"
            options={allGoodsGroup}
            onChange={e => handleInputChange(e)}
          />

          <SelectComponent
            lableValue="وضعیت رهگیری:"
            selectName="delivery"
            selectId="delivery"
            options={
              [
                {
                  "name": "بدون رهگیری",
                  "value": "bedon_rahgiri"
                },
                {
                  "name": "سریال",
                  "value": "serial"
                }
              ]
            }
            onChange={e => handleInputChange(e)}
          />
          <CheckBoxComponent
            lableValue="دارای ویژگی می باشد"
            inputName="has_feature"
            onChange={e => handleInputChange(e)}
          /> */}
        </form>
        <InputDynamicRowForEntryDocument
          // allCommodity={allCommodity}
          inputFields={inputFields}
          setInputFields={setInputFields}
          addFields={addFields}
          removeFields={removeFields}
          handleInputChangeCommodityRowOptions={
            handleInputChangeCommodityRowOptions
          }
          handleInputChangeCommodityRowInput={
            handleInputChangeCommodityRowInput
          }
          selectedRowInTable={selectedRowInTable}
          setSelectedRowInTable={setSelectedRowInTable}
        />
        <button className="bi-custom-button" onClick={createCommodityAndServicesGroupHandler}> ثبت </button>

      </div>
    </div>
  );
};

export default CreateWarehouseEntryDocument;
