
import { useState, usec } from 'react';
import axiosInstance from 'middleware/axiosInstance';
import useAxios from 'middleware/useAxios';
import InputComponent from 'components/Form/InputComponent'
import SelectComponent from 'components/Form/SelectComponent';
import addPlusIcon from 'assets/images/wareHouse-icons/add-plus.svg';
import DeleteRemoveTrashIcon from 'assets/images/wareHouse-icons/delete-remove-trash.svg';
import "./InputDynamicRowForEntryDocument.scss"

const InputDynamicRowForEntryDocument = (
    { setInputFields,
        addFields,
        removeFields,
        handleInputChangeCommodityRowInput,
        handleInputChangeCommodityRowOptions,
        inputFields,
        selectedRowInTable,
        setSelectedRowInTable
    }

) => {

    // test start
    const handleDoubleClick = (event, index) => {
        if (event.detail > 1) {
            // console.log(`Key pressed: ${event.key}`);
            setSelectedRowInTable(index)
            // console.log(index)
        }
        // console.log(event);
    }

    // useEffect(() => {
    //     // attach the event listener
    //     document.addEventListener('keydown', handleKeyPress);

    //     // remove the event listener
    //     return () => {
    //         document.removeEventListener('keydown', handleKeyPress);
    //     };
    // }, [handleKeyPress]);
    // test end




    const [allCommodity,
        errorGetAllCommodity,
        loadingGetAllCommodity,
        refetchAllCommodity] = useAxios({
            axiosInstance: axiosInstance,
            method: 'GET',
            url: '/commodity',
            requestConfig: {
                // headers: {
                //   'Content-Language': 'en-US',
                //   'Accept': 'text/html'
                // }
            }
        });

    const [entryCommodity, setEntryCommodity] = useState([])
    return (
        <div className='  '>

            < table className='bi-table-style ' >
                <thead className=''>
                    <th>

                    </th>
                    <th>
                        ردیف
                    </th>
                    <th>
                        شناسه
                    </th>
                    <th>
                        نام کالا/خدمات
                    </th>
                    <th>
                        نام واحد
                    </th>
                    <th>
                        مقدار ورود
                    </th>
                    <th>
                        فی
                    </th>
                    <th>
                        مبلغ
                    </th>
                    <th>
                        کد پیگیری
                    </th>
                    <th>
                        تاریخ پیگیری
                    </th>
                    <th>
                        شرح
                    </th>
                </thead>
                <tbody>
                    {inputFields.map((commodity, index) => {
                        return (
                            <>
                                {
                                    selectedRowInTable === index ?

                                        <tr key={index} onClick={(event) => handleDoubleClick(event, index)}>
                                            <td>
                                                <img src={DeleteRemoveTrashIcon}
                                                    width={22}
                                                    alt="remove-icon"
                                                    onClick={() => removeFields(index)} />
                                            </td>
                                            <td>
                                                {index + 1}
                                            </td>
                                            <td>
                                                <select name="commodityName" id="commodityName"
                                                    onChange={event => handleInputChangeCommodityRowOptions(index, event)}
                                                >
                                                    <option hidden  >
                                                        {
                                                            allCommodity ?
                                                                "انتخاب کنید" :
                                                                "درحال بارگذاری ..."
                                                        }
                                                    </option>
                                                    {allCommodity &&
                                                        allCommodity.map((commodity, index) => {
                                                            return (
                                                                <option key={commodity.id} value={JSON.stringify(commodity)}>{`${commodity.shenaseh} - ${commodity.name}`}</option>
                                                            )
                                                        })
                                                    }
                                                </select>
                                            </td>

                                            <td>
                                                {inputFields[index].name}
                                            </td>
                                            <td>
                                                {inputFields[index].unit}
                                            </td>
                                            <td>
                                                <InputComponent
                                                    inputName="count"
                                                    inputId="count"
                                                    inputType="number"
                                                    onChange={(event) => handleInputChangeCommodityRowInput(index, event)}
                                                />
                                            </td>
                                            <td>
                                                <InputComponent
                                                    inputName="fi"
                                                    inputId="fi"
                                                    inputType="number"
                                                    onChange={(event) => handleInputChangeCommodityRowInput(index, event)}
                                                />
                                            </td>
                                            <td>
                                                <InputComponent
                                                    inputName="price"
                                                    inputId="price"
                                                    inputType="number"
                                                    onChange={(event) => handleInputChangeCommodityRowInput(index, event)}
                                                />
                                            </td>
                                            <td>
                                                <InputComponent
                                                    inputName="code_delivery"
                                                    inputId="code_delivery"
                                                    inputType="number"
                                                    onChange={(event) => handleInputChangeCommodityRowInput(index, event)}
                                                />
                                            </td>
                                            <td>
                                                <InputComponent
                                                    inputName="date_delivery"
                                                    inputId="date_delivery"
                                                    inputType="date"
                                                    onChange={(event) => handleInputChangeCommodityRowInput(index, event)}
                                                />
                                            </td>
                                            <td>
                                                <InputComponent
                                                    inputName="description"
                                                    inputId="description"
                                                    onChange={(event) => handleInputChangeCommodityRowInput(index, event)}
                                                />
                                            </td>
                                        </tr>
                                        :
                                        <tr key={index} onClick={(event) => handleDoubleClick(event, index)}>
                                            <td>
                                                <img src={DeleteRemoveTrashIcon}
                                                    width={22}
                                                    alt="remove-icon"
                                                    onClick={() => removeFields(index)} />
                                            </td>
                                            <td>
                                                {index + 1}
                                            </td>
                                            <td>
                                                {commodity.commodityName}
                                            </td>
                                            <td>
                                                {inputFields[index].name}
                                            </td>
                                            <td>
                                                {inputFields[index].unit}
                                            </td>
                                            <td>
                                                {inputFields[index].count}

                                            </td>

                                            <td>
                                                {inputFields[index].fi}

                                            </td>

                                            <td>
                                                {inputFields[index].price}

                                            </td>

                                            <td>
                                                {inputFields[index].code_delivery}

                                            </td>
                                            <td>
                                                {inputFields[index].date_delivery}

                                            </td>
                                            <td>
                                                {inputFields[index].description}

                                            </td>

                                        </tr>
                                }
                            </>
                        )
                    })}
                </tbody>

            </table>
            <div className='w-100 d-flex justify-content-center'>

                <img
                    className='input-dynamic-row-for-entry-document mt-3'
                    src={addPlusIcon}
                    alt="add-icon"
                    onClick={addFields}
                />
            </div>
        </div>

    )
}

export default InputDynamicRowForEntryDocument