import { useState, useEffect } from "react";
import ButtonComponent from "../../../components/Form/ButtonComponent";
import InputComponent from "../../../components/Form/InputComponent";
import Axios from "../../../config/Axios";
import Loading from "../../../layouts/Loading/Loading";
import Modal from "../../../layouts/Modal/Modal"

const CommodityAndServicesGroup = () => {


  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState(null);
  const [allCommodityAndServicesGroup, setAllCommodityAndServicesGroup] = useState(null);
  const [newCommodityAndServicesGroup, setNewCommodityAndServicesGroup] = useState({});
  const [editeCommodityAndServicesGroup, setEditeCommodityAndServicesGroup] = useState({});



  const handleInputChange = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setNewCommodityAndServicesGroup({ ...newCommodityAndServicesGroup, [name]: value });
  };

  const changeEditeHandler = (e) => {
    const value = e.target.value;
    const name = e.target.name;
    setEditeCommodityAndServicesGroup({ ...editeCommodityAndServicesGroup, [name]: value });
  }


  const CreateCommodityAndServicesGroupHandler = () => {
    Axios.post("/goods_groups", newCommodityAndServicesGroup)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
        setShowAddModal(false)
      })
      .catch((res) => {
        console.log(res);
      })
  }

  const editeCommodityAndServicesGroupHandler = () => {
    Axios.put(`/goods_groups/${selectedItem.id}`, editeCommodityAndServicesGroup)
      .then((res) => {
        console.log(res);
        getAllCommodityAndServicesGroup();
        setShowEditModal(false)
      })
      .catch((res) => {
        console.log(res);
      })
  }

  const getAllCommodityAndServicesGroup = () => {
    Axios.get("/goods_groups")
      .then((res) => {
        setAllCommodityAndServicesGroup(res.data);
      })
      .catch(() => {
        setAllCommodityAndServicesGroup(false)
        alert("لیست انبارها دریافت نشد")
        console.log("err");
      })
  }

  const deleteCommodityAndServicesGroup = () => {
    Axios.delete(`/goods_groups/${selectedItem.id}`)
      .then(() => {
        getAllCommodityAndServicesGroup();
      })
      .catch((res) => {
        console.log(res);
      })
  }


  useEffect(() => {
    getAllCommodityAndServicesGroup()

  }, []);


  const deleteItemHandler = () => {
    if (window.confirm(`آیا دسته بندی "${selectedItem.name}" حذف گردد؟`)) {
      deleteCommodityAndServicesGroup();
    }
  }
  const handleSelectRow = (item) => {
    setSelectedItem(item)
  }

  return (
    <div className="customized-container">
      <div className="customized-wrapper">
        {showAddModal && <Modal>
          <div className="p-4">
            <h6>
              ایجاد گروه کالا و خدمات:
            </h6>
            <InputComponent
              inputName="code"
              inputId="code"
              lableValue="کد:"
              inputType="number"
              onChange={e => handleInputChange(e)}
            />

            <InputComponent
              inputName="name"
              inputId="name"
              lableValue="نام:"
              onChange={e => handleInputChange(e)}
            />
            <InputComponent
              inputName="shenaseh"
              inputId="shenaseh"
              lableValue="شناسه:"
              onChange={e => handleInputChange(e)}
            />

            <div className="d-flex flex-row justify-content-evenly mt-4">
              <ButtonComponent onClick={() => CreateCommodityAndServicesGroupHandler()} value="ثبت" />
              <ButtonComponent onClick={() => setShowAddModal(false)} value="لغو" />
            </div>

          </div>
        </Modal>}
        {showEditModal && <Modal>
          <div className="p-4">
            <h6>
              ویرایش گروه
              {` ${selectedItem.name}`}:
            </h6>
            <InputComponent
              inputName="code"
              inputId="code"
              lableValue="کد:"
              inputType="number"
              onChange={e => changeEditeHandler(e)}
              defaultValue={selectedItem.code}
            />

            <InputComponent
              inputName="name"
              inputId="name"
              lableValue="نام:"
              onChange={e => changeEditeHandler(e)}
              defaultValue={selectedItem.name}
            />
            <InputComponent
              inputName="shenaseh"
              inputId="shenaseh"
              lableValue="شناسه:"
              onChange={e => changeEditeHandler(e)}
              defaultValue={selectedItem.shenaseh}
            />

            <div className="d-flex flex-row justify-content-evenly mt-4">
              <ButtonComponent onClick={() => editeCommodityAndServicesGroupHandler()} value="ثبت" />
              <ButtonComponent onClick={() => setShowEditModal(false)} value="لغو" />
            </div>
          </div>

        </Modal>}

        <h5 className="title-session">
          گروه کالا و خدمات
        </h5>

        <div className='d-flex flex-row '>
          <button className='bi-custom-button'
            onClick={() => setShowAddModal(true)}
          >اضافه کردن</button>
          <button className='bi-custom-button'
            onClick={() => selectedItem ? setShowEditModal(true) : alert("جهت ویرایش  یک آیتم را انتخاب کنید!")}
          >ویرایش</button>
          <button className='bi-custom-button'
            onClick={() => selectedItem ? deleteItemHandler() : alert("جهت حذف یک آیتم را انتخاب کنید!")}
          >حذف</button>
        </div>


        {
          allCommodityAndServicesGroup &&
            allCommodityAndServicesGroup.length > 0 ?
            <table className="bi-table-style bi-row-select-enable col-12 col-md-6 col-lg-4">
              <thead>
                <tr>
                  <th >ردیف</th>
                  <th >کد</th>
                  <th >نام</th>
                  <th >شناسه</th>
                </tr>
              </thead>
              <tbody>
                {
                  allCommodityAndServicesGroup.map((CommodityAndServicesGroup) => {
                    return (
                      <tr id={CommodityAndServicesGroup.id}
                        onClick={() => handleSelectRow(CommodityAndServicesGroup)}
                        className={selectedItem ? CommodityAndServicesGroup.id === selectedItem.id ? "item-slected" : null : null}>
                        <td >
                          {CommodityAndServicesGroup.id}
                        </td>

                        <td >
                          {CommodityAndServicesGroup.code}
                        </td>

                        <td >
                          {CommodityAndServicesGroup.name}
                        </td>

                        <td >
                          {CommodityAndServicesGroup.shenaseh}
                        </td>

                      </tr>

                    )
                  })
                }

              </tbody>
            </table>
            :
            <Loading />
        }
      </div>
    </div>
  )
}

export default CommodityAndServicesGroup