const EntryDocumentFormValidation = (setError, mixNewWarehouseEntryDocumentWithCommodities) => {
    let nameError = "";
    let emailError = "";
    let passwordError = "";
    if (!mixNewWarehouseEntryDocumentWithCommodities.name) {
        nameError = "Name field is required";
    }
    const reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (!mixNewWarehouseEntryDocumentWithCommodities.email || reg.test(this.state.email) === false) {
        emailError = "Email Field is Invalid ";
    }
    if (!mixNewWarehouseEntryDocumentWithCommodities.password) {
        passwordError = "Password field is required";
    }
    if (emailError || nameError || passwordError) {
        setError({ nameError, emailError, passwordError });
        return false;
    }
    return true;
}
export default EntryDocumentFormValidation