import { useState } from 'react'
import InputComponent from '../../Form/InputComponent'
import deleteIcon from "../../../assets/images/wareHouse-icons/delete.svg"
import "./EachUnitEqulsTo.scss"
import SelectComponent from '../../Form/SelectComponent'
const EachUnitEqulsTo = ( {allMeasurementUnits, secondaryUnits, setSecondaryUnits} ) => {

    const [temporaryUnit, setTemporaryUnit] = useState("");
    const [temporaryValue, setTemporaryValue] = useState("");





    const handleAddUnit = () => {
        setSecondaryUnits([...secondaryUnits, { unit: temporaryUnit, value: temporaryValue }]);

    }

    const handleRemoveUnit = (selectedUnit) => {
        const temp = secondaryUnits.filter(item => item.value !== selectedUnit.value)
        setSecondaryUnits(temp)
        setTemporaryUnit("")
        setTemporaryValue("")
    }
    console.log(secondaryUnits);

    return (
        <>
            <InputComponent
                lableValue="هر"
                inputName="unitValue"
                inputId="unitValue"
                onChange={e => setTemporaryUnit(e.target.value)}
            />

            <SelectComponent
                lableValue="برابر است با یک"
                selectName="eachPrimaryUnitEqualTo"
                selectId="eachPrimaryUnitEqualTo"
                options={allMeasurementUnits}
                onChange={e => setTemporaryValue(`${e.target.options[e.target.selectedIndex].text}`)}
            />
            <button onClick={handleAddUnit}>
                اضافه کردن
            </button>
            {
                secondaryUnits[0] &&
                <table>
                    <tr>
                        <th>
                            ردیف
                        </th>
                        <th>
                            واحد فرعی
                        </th>
                        <th>
                            شرح
                        </th>
                        <th>
                            عملیات
                        </th>

                    </tr>
                    {secondaryUnits.map((unit, index) => {
                        return (
                            <tr key={unit.unit}>
                                <td>
                                    {index + 1}
                                </td>
                                <td>
                                    {unit.unit}
                                </td>
                                <td>
                                    {unit.value}
                                </td>
                                <td>
                                    <img
                                        className='deleteIcon'
                                        src={deleteIcon}
                                        onClick={() => handleRemoveUnit(unit)}
                                        alt="حذف" />
                                </td>
                            </tr>
                        )
                    })}
                </table>
            }

        </>
    )
}

export default EachUnitEqulsTo