import InputComponent from 'components/Form/InputComponent';
import React, { useState } from 'react'
import Modal from "../../../layouts/Modal/Modal"
// import InputForm from '../../../InputForm/InputForm';

const MeasurementUnit = () => {


  const [showModal, setShowModal] = useState(false);

  return (


    < div className="customized-container" >
      <div className="customized-wrapper">
        {showModal && <Modal>
          <label htmlFor="">نام:</label>
          <input type="text" />
          <label htmlFor="">تعداد اعشار:</label>
          <input type="text" />
          <label htmlFor="">توضیحات:</label>
          <textarea type="text" />

          <div className="d-flex flex-row">
            <button className='bi-custom-button'>ثبت</button>
            <button className='bi-custom-button' onClick={() => setShowModal(false)}>لغو</button>
          </div>

        </Modal>}
        <h5 className="title-session">
          سند اختتامیه انبار
        </h5>
        <button className='bi-custom-button'
          onClick={() => setShowModal(true)}> اضافه کردن  </button>


        <table className="bi-table-style">
          <thead>
            <tr>
              <th >آیدی</th>
              <th >نام</th>
              <th >تعداد اعشار</th>

            </tr>
          </thead>
          <tbody>
            <tr>
              <td>
                1
              </td>
              <td >
                متر
              </td>
              <td >
                0
              </td>
            </tr>
            <tr>
              <td >
                2
              </td>
              <td >
                سانتی متر
              </td>
              <td >
                2
              </td>
            </tr>
            <tr>
              <td >
                3
              </td>
              <td >
                میلی لیتر
              </td>
              <td >
                3
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div >
  )
}

export default MeasurementUnit