const CheckBoxComponent = (
    { inputName,
        lableValue,
        onChange }
) => {
    return (
        <div className="d-flex flex-row">
            <input type="checkbox" onChange={onChange} id={inputName} name={inputName} value={true} />
            <label htmlFor={inputName}>{lableValue}</label>
        </div>
    )
}

export default CheckBoxComponent